body {
  background-color: #e4e4e4;
}

.hidden {
  display: none;
}

body {
  font-family: "Open Sans", sans-serif;
  color: #444444;
  font-size: 16px;
  background-image: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0)), url(/assets/bg01.png);
}

h1 {
  font-size: 1.625rem;
  font-weight: 800;
}
@media (min-width: 768px) {
  h1 {
    font-size: 3.25rem;
  }
}

h2 {
  font-size: 1.375rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 2.5rem;
  }
}

.button {
  display: block;
  padding: 1rem;
  text-align: center;
  border-radius: 5px;
  text-decoration: none;
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .button {
    padding: 2rem;
  }
}
@media (min-width: 992px) {
  .button {
    flex-direction: row;
    justify-content: space-between;
    min-width: 15rem;
  }
}
@media (min-width: 1280px) {
  .button {
    min-width: 23.75rem;
  }
}
.button span,
.button i {
  font-size: 1.125rem;
}
@media (min-width: 768px) {
  .button span,
.button i {
    font-size: 2rem;
  }
}
.button span {
  font-weight: 800;
}
.button i {
  margin-right: 0.5rem;
}
@media (min-width: 992px) {
  .button i {
    margin-right: 0;
  }
}
.button--primary {
  color: #fff;
  background-color: #10a0d5;
}
.button--secondary {
  color: #444444;
  background-color: rgba(0, 0, 0, 0.085);
}

.header {
  margin-top: 3.375rem;
}
.header__logo {
  text-align: center;
}
@media (min-width: 992px) {
  .header__logo {
    display: flex;
    align-items: center;
  }
}
.header__brand {
  display: inline-block;
  background-color: #fc4887;
  font-family: "Oleo Script", cursive;
  color: #fff;
  font-size: 3.5rem;
  letter-spacing: 3px;
  padding: 0.5rem;
  border-radius: 5px;
}
.header__slogan {
  line-height: 3;
  font-weight: 800;
}
@media (min-width: 992px) {
  .header__slogan {
    margin-left: 1rem;
  }
}

.hero {
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .hero {
    margin-top: 2rem;
  }
}
.hero__wrapper {
  background: #fff;
  box-shadow: 3px 0px 0px rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  padding: 0.9375rem;
}
@media (min-width: 768px) {
  .hero__wrapper {
    padding: 4rem 3rem;
  }
}
@media (min-width: 992px) {
  .hero__wrapper {
    padding: 4.5rem;
  }
}
.hero__description {
  text-align: center;
}
@media (min-width: 992px) {
  .hero__description {
    text-align: left;
  }
}
.hero__description h1 {
  margin-top: 1rem;
  margin-bottom: 0.75rem;
}
.hero__description h2 {
  line-height: 1.5;
}
@media (min-width: 992px) {
  .hero__description h2 {
    padding-right: 1rem;
  }
}
.hero__cta {
  padding-top: 1rem;
}
@media (min-width: 992px) {
  .hero__cta {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
}
.hero__cta .button:first-child {
  margin-bottom: 1rem;
}

input#menu ~ nav {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.5s;
  position: absolute;
  right: 2rem;
  min-width: 50%;
  top: 7rem;
  box-shadow: -5px 6px 11px 0px #00000047;
}
@media (min-width: 992px) {
  input#menu ~ nav {
    overflow: visible;
    position: static;
    min-width: 100%;
    min-height: 100%;
    box-shadow: none;
  }
}
input#menu:checked ~ nav {
  max-height: 25rem;
}

.menu {
  display: flex;
  align-items: center;
}
.menu__burger {
  position: absolute;
  right: 2rem;
  top: 5rem;
}
@media (min-width: 992px) {
  .menu__burger {
    display: none;
  }
}
.menu__burger span {
  display: block;
  width: 1.5rem;
  height: 2px;
  background: #000;
  margin-bottom: 3px;
}
.menu__list {
  background-color: #fff;
  border-radius: 5px;
  width: 100%;
}
@media (min-width: 992px) {
  .menu__list {
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-weight: 800;
  }
}
.menu__item {
  text-align: center;
  line-height: 2.5;
}
@media (min-width: 992px) {
  .menu__item {
    padding: 0 0.5rem;
    transition: background-color, color 0.4s;
    cursor: pointer;
  }
}
@media (min-width: 992px) {
  .menu__item:hover {
    background-color: #444444;
    color: #fff;
    border-radius: 5px;
  }
}

.article {
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .article {
    margin-top: 2rem;
  }
  .article__box {
    padding: 0;
  }
  .article__wrapper {
    display: flex;
    justify-content: space-between;
  }
}
.article__box {
  margin-top: 1rem;
}
.article__content {
  background: #fff;
  box-shadow: 3px 0px 0px rgba(0, 0, 0, 0.05);
  border-radius: 5px;
}
.article img {
  width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.article__description {
  padding: 1.875rem 0.9375rem;
  text-align: left;
}
.article h1 {
  font-size: 1.2375rem;
  margin-bottom: 4rem;
}
.article p {
  font-size: 0.9rem;
  margin-top: 2rem;
  line-height: 2;
}

.info {
  background-color: #ffffff;
}
.info img {
  border-radius: 5px;
  width: 100%;
  margin-bottom: 0.5rem;
}
.info__row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
}
@media (min-width: 1280px) {
  .info__row {
    margin-bottom: 0.5rem;
  }
}
.info h1 {
  font-size: 1rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .info h1 {
    font-size: 2rem;
  }
}
.info h2 {
  font-size: 1.25rem;
  font-weight: 800;
  margin: 2rem 0 1rem;
}
@media (min-width: 992px) {
  .info h2 {
    margin: 0.5rem 0 2rem 0;
  }
}
.info__container {
  margin-top: 4rem;
}
.info p {
  line-height: 2;
  margin-bottom: 2rem;
}
.info .separator {
  width: 100%;
  height: 2rem;
  border-bottom: 1px solid lightgrey;
}
@media (min-width: 992px) {
  .info .separator {
    display: none;
  }
}
.info__wrapper {
  background: #fff;
  padding: 1.875rem 0.9375rem;
  margin-top: 4rem;
}
@media (min-width: 992px) {
  .info__wrapper {
    flex-direction: row-reverse;
  }
}
@media (min-width: 992px) {
  .info__button {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    min-width: 0;
  }
}
@media (min-width: 992px) {
  .info__button:first-of-type {
    max-width: 14rem;
    max-height: 0.2rem;
  }
}
@media (min-width: 992px) {
  .info__button:last-child {
    max-width: 5rem;
    max-height: 0.2rem;
  }
}
@media (min-width: 768px) {
  .info i, .info span {
    font-size: 1rem;
  }
}
@media (min-width: 992px) {
  .info i, .info span {
    font-size: 1.25rem;
  }
}

.links h1 {
  padding: 1.875rem 1.875rem 1.5rem;
  font-size: 1rem;
}
.links a {
  display: block;
  line-height: 2;
  font-size: 0.9rem;
  padding: 0 2rem;
  color: #696969;
}
.links .separator {
  width: 90%;
  margin-left: 1.875rem;
  margin-right: 1.875rem;
  height: 2rem;
  border-bottom: 1px solid lightgrey;
}
@media (min-width: 992px) {
  .links .separator {
    display: none;
  }
}
.links__icons {
  padding: 0 1.875rem;
}
.links p {
  padding: 0 1.875rem;
  line-height: 2;
}
.links i {
  margin-bottom: 1rem;
  transition: background-color 0.25s ease-in-out;
  display: inline-block;
  width: 2.5rem;
  height: 2.5rem;
  text-align: center;
  line-height: 2.5rem;
  border-radius: 0.35rem;
  outline: 0;
  opacity: 0.75;
  text-decoration: none;
  background: #222222;
  color: white;
}

.footer {
  padding: 1rem 1.5rem;
}
.footer ul {
  line-height: 2;
}
@media (min-width: 1280px) {
  .footer ul {
    display: flex;
    justify-content: center;
  }
}
@media (min-width: 1280px) {
  .footer li:nth-child(2), .footer li:last-child {
    border-left: solid 1px lightgrey;
    padding: 0 1rem 0 1rem;
  }
}
@media (min-width: 1280px) {
  .footer li:first-child {
    padding: 0 1rem 0 0;
  }
}
.footer a {
  color: #444444;
}
@media (min-width: 768px) {
  .footer {
    text-align: center;
  }
}